import React from 'react';
import './App.css';
import Navbar from './components/Navbar';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import Home from "./components/pages/Home"
import ContactUs from "./components/pages/ContactUs"
import References from "./components/pages/References"
import SignUp from "./components/pages/SignUp"

function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Switch>
          <Route path="/Formfix2/" exact component={ Home } />
          <Route path="/Formfix2/contact-us" exact component={ ContactUs } />
          <Route path="/Formfix2/references" exact component={ References } />
          <Route path="/Formfix2/sign-up" exact component={ SignUp } />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
